import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'

import store from 'store/index.js'
import './common/uni.css'

// 引入自己封装的api
import api from './common/api.js'
Vue.prototype.api = api

// 引入uview
import uView from './uni_modules/uview-ui'
Vue.use(uView)





import Vue from 'vue'
Vue.config.productionTip = false
App.mpType = 'app'
Vue.prototype.$toast = function(title,duration=850,icon='none'){
	uni.showToast({
		title: title,
		icon:icon,
		duration:duration
	});
}
const app = new Vue({
	store,
    ...App,
	beforeCreate(){
		Vue.prototype.$bus = this;
	}
})
app.$mount()